import React from 'react';
import { DefaultButton } from '@fluentui/react/lib/Button';


const NavigationBar = ({ navigateTo }) => {
const handleExternalLink = (url) => {
    window.open(url, '_blank'); // '_blank' opens the link in a new tab
  };

return (
 <nav style={{ margin: '20px', display: 'flex', justifyContent: 'space-evenly' }}>
      <DefaultButton text="Home" onClick={() => navigateTo('Home')} />
      <DefaultButton text="Support" onClick={() => navigateTo('Support')} />
      <DefaultButton text="Privacy" onClick={() => navigateTo('Privacy')} />
      <DefaultButton text="X-Scan" onClick={() => navigateTo('X-Scan')} />
      <DefaultButton text="Turbo-K" onClick={() => handleExternalLink('https://law-gpt.dmcomputerservices.net')} />
     {/* Add other navigation buttons as needed */}
    </nav>
  );
};




export default NavigationBar;
